.video-card-detail{
  position: relative;
  @media screen and (max-width: 575px) {
    width: 100%;
    height: 105px;
  }
  .product-img{
    height: 180px;
    width: 315px;
    border-radius: 15px;
    z-index: 0;
    position: relative;
    @media screen and (max-width: 575px) {
      width: 100%;
      height: 105px;
      object-fit: cover;
    }

  }
  .icon-css{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 2;
    @media screen and (max-width: 575px){
      img{
        width: 30px;
      }
    }
  }
  .layer{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 1;
    height: 180px;
    width: 100%;
    border-radius: 15px;
    background-color: rgba(0,0,0,0.5);
    @media screen and (max-width: 575px) {
      width: 100%;
      height: 100%;
    }
  }
}

.modal {
  background: rgba(0,0,0,0.8);
  z-index: 1010;
}
