:root {
  --secondary: #F6F6F6;
  --red: #FF3D3D;
  --yellow: #FCAE2A !important;
  --black: #333333;
  --blue: #5F78BE;
  --gray: #D2D2D2;
  --green: #0AAF51;
  --dark-gray: #9B9B9B;
  --dark-section: #3C4041;
  --light-gray: #73788B;
  --light-pink: #FFF5E4;
  --light-green: #5DD97E;
  --light-blue: #E7EBF5;
  --light-yellow: #FFF3DF;
  --light-red: #E64C4C;
  --off-white-red: #FD4B2D;
}

* {
  margin: 0;
  padding: 0;
}

body, html {
  width: 100%;
  height: 100%;
  padding-top: 65px;
  background-color: var(--secondary) !important;
  scroll-behavior: smooth;
  @media screen and (max-width: 575px) {
    padding-top: 90px;
