.input-with-label {
  input{
    width: 100%;
    height: 60px;
    border: 1px solid #E2E2E2;
    border-radius: 30px;
    padding: 0 30px 0 60px;
    position: relative;
    font-family: 'Poppins-semibold';
    font-size: 14px;
    &:focus{
      border-color: var(--yellow);
    }

  }
  input, textarea{
    &:focus {
      box-shadow: none;
    }
  }
}
