.cart-modal-main {
  .overlay {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    z-index: 9;
    background-color: rgba(0, 0, 0, 0.5);
  }
}

.cart-modal {
  width: 500px;
  height: 100%;
  position: fixed;
  top: 0;
  right: 0;
  z-index: 16;
  transition: ease all .6s;
  transform: translate(100%, 0);

  @media screen and (max-width: 991px) {
    width: 100%;
  }

  .cart-detail {
    height: calc(100vh - 195px);
    overflow-y: scroll;
    overflow-x: hidden;
    position: relative;
    background-color: var(--white);

    .qty-box {
      select {
        border-radius: 5px;
        border: 1px solid #73788B;
        font-family: 'Roboto-medium';
        font-size: 14px;
        width: 70px;
        height: 25px;
        padding: 0 10px 0 5px;
        -webkit-appearance: none;
        background-image: url("../../../assets/imgs/select-drop.svg");
        background-repeat: no-repeat;
        background-position: 95%;

        &:focus {
          outline: none;
        }
      }
    }
  }

  .checkout-main {
    position: fixed;
    bottom: 0;
    width: 500px;
    @media screen and (max-width: 991px) {
      width: 100%;
    }

    .checkout-btn {
      height: 45px !important;
      width: auto;
      box-shadow: 0px 10px 25px #FCAE2A33 !important;
    }
  }
}

.cart-active {
  transform: translate(0, 0);
}

.shadow {
  box-shadow: 0 5px 3px 0 rgba(0, 0, 0, 0.04), 0 1px 2px 0 rgba(0, 0, 0, 0.06) !important;
}
.flat-text {
  p {
    position: relative;
    height: 25px;
    padding-left: 30px;
    padding-right: 10px;
    background: #0AAF51;
    display: flex;
    align-items: center;
    justify-content: center;
    @media screen and (max-width: 576px){
      padding-left: 23px !important;
    }

    &:after {
      content: '';
      position: absolute;
      left: -1px;
      width: 0;
      height: 0;
      border-top: 20px solid transparent;
      border-left: 20px solid var(--white) !important;
      border-bottom: 20px solid transparent;
    }
  }
}


.cart-add {
  align-items: center;
  display: flex;
  height: 26px;
  background-color: #fff;
  font-size: 14px;
  text-align: left;
  color: #1b2224;


  .cart-quantity-btn {
    height: 28px;
    width: 28px;
    border: 1px solid #babcc5;
    cursor: pointer;
    @media screen and (max-width: 576px){
      width: 20px;
      height: 21px;
    }
  }
}

.cart-product {
  width: 120px;
  height: 120px;
  object-fit: cover;
  @media screen and (max-width: 576px){
    width: 40px;
    height: 60px;
  }
}

.out-of-stock-main{
  width: 100%;
  height: 100%;
  margin: 0 auto;
  background: rgba(0, 0, 0, 0.8);
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 1;
  @media only screen and (min-width: 768px) and (max-width: 991px){
    width: 200px;
  }
  .sold-out{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50% , -50%);
    background-color: var(--red);
    border-radius: 50px;
    border: none;
    width: 120px;
    height: 35px;
  }
}

.delete-cart-item {
  z-index: 6;
  position: relative;
  background: white;
  border-radius: 100%;
}

.no-items{
  height: calc(100vh - 125px);
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.move-to-wishlist {
  position: relative;
  z-index: 2;
  background: white;
}

.checkout-main{
  .slick-prev{
    left: -40px !important;
  }
  
  .slick-slider .slick-track{
    line-height: 0px !important;
  }
  
  .lh-16{
    line-height: 16px;
  }
  
  .lh-24{
    line-height: 20px;
  }
}

