.custom-datepicker {
  .input-with-label {
    position: relative;
  }
  .calendar {
    position: absolute;
    right: 1.3rem;
    z-index: 5;
  }
}

.react-datepicker-wrapper {
  width: 100%;

  .react-datepicker__close-icon {
    background-color: transparent;
    border: 0;
    outline: 0;
    padding: 0px 6px 0px 0px;
    position: absolute;
    top: -2px;
    right: 12px;
    display: table-cell;
    vertical-align: middle;
    z-index: 2;

    &:after {
      background-color: #73788b;
      color: #fff;
      height: 25px;
      width: 25px;
      padding: 2px;
      font-size: 12px;
    }
  }
}

.form-control:disabled, .form-control[readonly] {
  opacity: 1;
  background: transparent;
}
