.payment-status{
  position: absolute;
  max-width: 500px;
  width: 90%;
  margin: 0 auto 30px;
  left: 0;
  right: 0;
  top: calc(100% - 80%);
  background-image: linear-gradient(225deg, #fcae2ad1, #fcae2a);
  padding: 5px;
  border-radius: 12px;
  box-shadow: 0px 0px 7px 1px #00000059
}
.payment-status-app{
  padding: 20px;
  transform: translate(0, 30%);
}
.payment-status-app .payment-card-image{
  max-width: 130px;
}
.payment-status>div{
  width: 100%;
  height: 100%;
  padding-bottom: 20px;
  border-radius: 10px;
  background-color: white;
}
.payment-card-image{
  width: 60%;
  margin-left: auto;
  margin-right: auto;
  padding: 20px;
  display: block;
  position: relative;
}
.payment-card-image img{
  width: 100%;
}
.payment-card-image .status{
  margin: auto;
  width: 50px;
  position: absolute;
  right: 5px;
  bottom: 50px;
}
.payment-text{
  text-align: center;
}
.payment-text h2{
  margin-bottom: 10px;
  font-size: 25px;
  font-weight: bold;
  text-align: center;
}
.payment-text p{
  margin-bottom: 35px;
}
