.categories-product-main {
  .category-product-item {
    height: 100px;
    width: 100%;
    margin: 0 auto;
    display: flex;
    align-items: center;
    justify-content: center;
    img{
      height: 100%;
      object-fit: contain;
      border-radius: 20px;
      @media screen and (max-width: 575px){
        object-fit: unset;
        border-radius: 20px;
      }
    }
    @media only screen and (max-width: 575px) {
      height: 60px;

    }
  }

  .custom-tooltip-main{
    position: relative;
    word-break: break-word;
    p{
      text-overflow: ellipsis;
      overflow: hidden;
    }
    .custom-tooltip{
      display: none;
      padding: 5px 15px;
      text-align: center;
    }
    &:hover{
      .custom-tooltip{
        position: absolute;
        left: 0;
        right: 0;
        display: block;
        min-height: 30px;
        width: 100%;
        background: rgba(0,0,0,0.8);
        border-radius: 5px;
        z-index: 1;
        &:after{
          content: '';
          position: absolute;
          left: 150px;
          width: 0;
          top: -15px;
          height: 0;
          border-top: 10px solid transparent;
          border-left: 10px solid rgba(0,0,0,0.8);
          border-bottom: 10px solid transparent;
          transform: rotate(-90deg);
        }
      }
    }
  }
}
