.whey-product-main {
  .whey-product-card{
    z-index: 1;
    position: relative;
    width: 196px;
    min-height: 231px;
    margin: 0 auto;
    @media only screen and (max-width: 767px){
      width: 160px;
      min-height: 195px;
    }
    @media only screen and (min-width: 768px) and (max-width: 991px){
      width: 200px;
    }
    @media only screen and (min-width: 1024px) and (max-width: 1210px){
      width: 185px;
      min-height: 210px;
    }
    @media only screen and (max-width: 370px){
      width: 135px;
    }
    p{
      word-break: break-word;
    }
    .whey-product-img {
      margin-top: -50px;
    }
    .product-rating{
      border: 1px solid var(--secondary);
      border-radius: 40px;
      font-family: 'Poppins-medium';
      font-size: 12px;
      padding: 2px 10px;
    }

    .custom-offer-label{
      position: absolute;
      top: 10px;
      right: 0;
      transform: rotate(-90deg);
      height: 22px;
      width: 42px;
      background-color: var(--green);
      padding-left: 10px;
      display: flex;
      align-items: center;
      justify-content: center;
      @media only screen and (max-width: 767px){
        top: 12px;
        right: 0;
      }
      @media only screen and (min-width: 768px) and (max-width: 991px){
        right: 5px;
      }
      span{
        font-family: 'Poppins-medium';
        font-size: 10px;
        color: var(--white);
      }
      &:after{
        content: '';
        position: absolute;
        left: -7px;
        width: 0;
        height: 0;
        transform: rotate(90deg);
        border-left: 12px solid transparent;
        border-right: 12px solid transparent;
        border-bottom: 11px solid var(--white);
        @media only screen and (max-width: 767px){
          left: -8px;
        }

      }
    }
    .like-main {
      z-index: 5;
      position: absolute;
      left: 10px;
      top: 5px;
      @media only screen and (max-width: 767px){
        left: 8px;
        top: 7px;
      }
      @media only screen and (min-width: 768px) and (max-width: 991px){
        left: 5px;
      }
      img {
        z-index: 5;
        position: relative;
      }
    }
  }

  .out-of-stock-main{
    width: 196px;
    height: 100%;
    margin: 0 auto;
    background: rgba(0, 0, 0, 0.7);
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: 1;
    @media only screen and (max-width: 767px){
      width: 160px;
    }
    @media only screen and (min-width: 768px) and (max-width: 991px){
      width: 200px;
    }
    @media only screen and (min-width: 1024px) and (max-width: 1210px){
      width: 185px;
    }
    @media only screen and (max-width: 370px){
      width: 135px;
    }
    .sold-out{
      position: absolute;
      top: 80%;
      left: 50%;
      transform: translate(-50% , -50%);
      background-color: var(--red);
      border-radius: 50px;
      border: none;
      width: 120px;
      height: 35px;
    }
  }

  .product-img {
    height: 110px;
    width: 110px;
    object-fit: contain;
    @media only screen and (max-width: 767px){
      height: 90px;
      width: 90px;
    }
  }

  .text-ellipse-custom {
    display: block;
    display: -webkit-box!important;
    overflow: hidden;
    text-overflow: ellipsis;
    -webkit-line-clamp: 2;
    max-height: 39px;
    min-height: 39px;
    -webkit-box-orient: vertical;
    @media screen and (min-width: 320px) and (max-width: 767px) {
      max-height: 39px;
      min-height: 39px;
    }
  }

}
